<template>
    <van-popup  v-model="thisVisible" 
                   position="left">
        <van-nav-bar :fixed="true" 
                    title="Category"
                    @click-right="goBack">
                <span  slot="right">
                    back
                    <van-icon size="1.3em" name="arrow-left"/>
                </span>
            </van-nav-bar>
           
           <div   class="panel-with-nav-bar fix-left">
               <van-badge-group ref="categoryPanelLeftMenu" :active-key="activeCategoryIndex.level1" @change="changeCategory">
                  
                        <van-badge  v-for="(item,index) in goodsCategory" :title="item" :key="index">
                        </van-badge>
                  
               </van-badge-group>

                <category-list-detail class="category-list-detail"  ref="categoryPanelRightMenu" :categoryList="selectedCategoryList"
                                        :categoryListLength="selectedCategoryList.length">
                </category-list-detail>
            



           </div>
    </van-popup>
</template>

<script>
import CategoryListDetail from '@/components/home/sub-pages/base-components/category/category-list-detail';

export default {

    model:{
        prop:'visible',
        event:'changeStatus'
        },

    props:{
        visible:{
            type:Boolean,
            default:false,
        }
    },
    components:{
        CategoryListDetail,
    },
    data:function(){
        return{
            openCount:0,
            thisVisible:this.visible,
            goodsCategory:['1','2','3','4'],
            selectedCategoryList:[],
            //选中的商品一级分类索引
            activeCategoryIndex:{
                level1:0,
                level2:0
                },

        }
    },

    watch:{
        //vant的badge组件有问题 change事件实际上是click ，所以在此手动添加监控，监控一级菜单选中项的变化
        'activeCategoryIndex.level1':{
            handler: function(val){
                
                console.log("change:" +this.activeCategoryIndex.level1+"----"+val);
                var _this=this;
                this.axios({
                    type:"get",
                    url:"pro/category/getList",
                }).then(function(rep){
                    _this.selectedCategoryList=rep.data.data;
                //console.log(_this.selectedCategoryList.length);
                }).catch(function(error){

              })
            }

        },
        visible:function(val){
            this.thisVisible=val; 
        },
        thisVisible:function(val){
            if(!val)
                this.$emit('changeStatus',val);
            else{
                //当该界面第一次打开时调用
                if(this.openCount==0)
                {
                    //当这个组件受 v-if v-show 影响时，mounted里面使用ref的方式可能获取不到元素 
                    this.$nextTick(()=>{
                        this.$refs.categoryPanelLeftMenu.$el.style.height=window.innerHeight-56+'px';
                        this.$refs.categoryPanelRightMenu.$el.style.height=window.innerHeight-56+'px';
                        //console.log(window.innerHeight-56)
                    })
                }
                this.openCount++;
            }
        }
    },
    
    mounted(){
        this.init();
    },

    methods:{
        init:function(){
              
              var _this=this;
              this.axios({
                  type:"get",
                  url:"pro/category/getList",
              }).then(function(rep){
                _this.selectedCategoryList=rep.data.data;
               console.log(_this.selectedCategoryList.length);
              }).catch(function(error){

              })
              




        },

        goBack:function(){
            this.thisVisible=false;
        },
         
        changeCategory:function(key){
             this.activeCategoryIndex.level1=key;

            //console.log(this.activeCategoryIndex.level1);
        }
    }
    
}
</script>

<style lang="less" scoped>
    
    .van-popup{
        .van-nav-bar__right{
            color:#1989fa;
        }

        .panel-with-nav-bar{
            .van-badge-group{
                overflow: hidden;
                overflow-y:scroll;
                width:85px;
                float:left;
               
                .van-badge{
                    border-bottom: 1px solid #E1E1E1;
                }
            };
            .category-list-detail{
                float:right;
                overflow: hidden;
                overflow-y:scroll;
                position:absolute;
                left:85px;
                right:0px;
            }


        }


    }
</style>



